body {
  background: url(/static/media/koromo.preload.eed92857.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  font-family: "Microsoft YaHei", "Meiryo", "Segoe UI", sans-serif;
}
.page-loading {
  height: 100vh;
  align-items: center;
}
h1 a,
h1 a:hover {
  color: inherit;
  text-decoration: none;
}
.ReactVirtualized__Table__row.even {
  background-color: rgba(0, 0, 0, 0.05);
}
.ReactVirtualized__Table__headerRow,
.ReactVirtualized__Table__row {
  border-top: 1px solid #dee2e6;
}
.ReactVirtualized__Table__headerRow span {
  display: block;
}
@-webkit-keyframes placeHolderShimmer {
  0% {
    background-position: -800px 0;
  }
  100% {
    background-position: 800px 0;
  }
}
@keyframes placeHolderShimmer {
  0% {
    background-position: -800px 0;
  }
  100% {
    background-position: 800px 0;
  }
}
.ReactVirtualized__Table__row.loading {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: placeHolderShimmer;
          animation-name: placeHolderShimmer;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background: #f6f7f8;
  background: -webkit-gradient(linear, left top, right top, color-stop(8%, #eeeeee), color-stop(18%, #dddddd), color-stop(33%, #eeeeee));
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  background-size: 800px 104px;
}
.ReactVirtualized__Table__row.loading:nth-of-type(odd) {
  -webkit-animation-delay: -0.75s;
          animation-delay: -0.75s;
}
.recharts-label {
  pointer-events: none;
}
.recharts-pie-label-text {
  font-weight: bold;
}
.with-active-player .player:not(.active-player) {
  color: #aaa;
}
.with-active-player .player:not(.active-player) a {
  color: inherit;
}
.player-chart-tooltip {
  margin: 0;
  padding: 20px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  display: block;
  border: 0 none;
  border-radius: 5px;
}
.player-chart-tooltip p {
  margin: 0;
}
.player-chart-tooltip .active-player {
  color: inherit;
  text-decoration: underline;
}
.player-chart-tooltip a {
  color: inherit;
}

